import * as React from "react";

/**
 * 进度条控件
 * 有限的进度条控件，通过设置value，可以使其显示一个统一外观的进度条
 */
export interface IProgressBarProp {
    /**
     * 当前进度的位置
     */
    value?: number;
    /**
     * 允许用户选择不同的颜色
     * @TODO
     */
    color?: string;
    /**
     * 最小值，默认为0
     */
    minimum?: number;
    /**
     * 最大的进度值，默认值：100
     */
    maximum?: number;
    /**
     * 可定义进度条的样式，如上下边距
     */
    style?: React.CSSProperties;
    /**
     * 是否为不确定的进度条
     */
    isIndeterminate?: boolean;
}
export interface IProgressBarState {
    none?: any;
}
export class ProgressBar extends React.Component<IProgressBarProp, IProgressBarState> {
    private style = require("./style");
    public render() {
        return <div
            className={this.style.box}
            style={this.props.style}
        >
            <progress

                value={this.props.value}
                max={this.props.maximum ? this.props.maximum : 100}
                className={this.style.progress}>您的浏览器不支持progress</progress>
        </div>;
    }
}



